  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff"); }

/* Open Sans Light */
@font-face {
  font-family: "Open Sans";
  font-weight: 300;
  font-style: normal;
  src: url("../fonts/opensanslight.woff2") format("woff2"), url("../fonts/opensanslight.woff") format("woff"); }

/* Global Tags & Classes */
a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  display: block; }

/* Class Visually Hidden */
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

/* Class Hidden */
.hidden {
  display: none; }

/* logo - svg position */
.logo {
  width: 147px;
  height: 40px; }
  .logo--top {
    margin: 5px 0 0 -71px;
    fill: #ffffff; }
    .logo--top:hover {
      fill: #d22856; }
    .logo--top:active {
      fill: rgba(255, 255, 255, 0.3); }
    @media (min-width: 660px) {
      .logo--top {
        margin: 27px 0 42px 0; } }
    @media (min-width: 960px) {
      .logo--top {
        margin: 27px 173px 43px 0; } }
  .logo--bottom {
    fill: #283645; }
    .logo--bottom:hover {
      fill: #d22856; }
    .logo--bottom:active {
      fill: rgba(255, 255, 255, 0.3); }

/* logo svg viewport */
.logo__glasses-tablet {
  display: none; }
  .logo__glasses-tablet--footer {
    display: block; }
    @media (min-width: 960px) {
      .logo__glasses-tablet--footer {
